import React, { useState } from "react";
import '../../assets/css/RegistrationForm.css';
import { authLogin } from "../../features/apiCalls";

const LoginForm = () => {
  const [formData, setFormData] = useState({
    username: "",
    password: "",
  });
  const [error, setError] = useState("");

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");

    if (!formData.username || !formData.password) {
      setError("Tn ng nhp v mt khu khng c  trng");
      return;
    }

    try {
      const response = await authLogin(formData);
      localStorage.setItem("isLogin", "true");
      window.location.href = "/qlkh";
    } catch (error) {
      setError("ng nhp tht bi. Vui lng kim tra li thng tin ng nhp.");
      console.error("Login error:", error);
    }
  };

  return (
    <div className="login-container">
      <h1>ng Nhp</h1>
      <form className="login-form" onSubmit={handleSubmit}>
        <div>
          <label>Tn ng nhp:</label>
          <input
            type="text"
            placeholder="Nhp tn ng nhp"
            name="username"
            value={formData.username}
            onChange={handleInputChange}
            required
          />
        </div>
        <div>
          <label>Mt khu:</label>
          <input
            type="password"
            placeholder="Nhp mt khu"
            name="password"
            value={formData.password}
            onChange={handleInputChange}
            required
          />
        </div>
        {error && <p className="error-message">{error}</p>}
        <button type="submit">ng nhp</button>
      </form>
      <div className="login-link">
        Bn  c ti khon ? <a href="/register">ng k</a>
      </div>
      <style jsx>{`
        h1 {
          margin-bottom: 30px;
          margin-top: 30px;
          font-size: 32px;
          text-align: center;
          color: #333;
        }
        .login-link {
          text-align: center;
          margin-top: 20px;
          font-size: 14px;
        }
        .login-link a {
          color: #0149cd;
          text-decoration: none;
        }
        .login-link a:hover {
          text-decoration: underline;
        }
      `}</style>
    </div>
  );
};

export default LoginForm;
